



body {
  font-size: 16px; }

h1, .h1 {
  font-weight: 500;
  color: #60666b;
  font-size: 2.2em; }

h2, .h2 {
  font-weight: 500;
  color: #32C7CA;
  font-size: 1.2em;
  letter-spacing: 0;
  text-transform: none; }

h3, .h3 {
  font-weight: 900;
  color: #4F4F4F;
  text-transform: uppercase;
  font-size: .75em;
  margin-bottom: .25em;
  letter-spacing: .1em; }

h3 + *, .h3 + * {
  margin-top: 0; }

@media screen and (min-width: 768px) {
  body {
    font-size: 20px; } }
@media screen and (min-width: 1300px) {
  h1, .h1 {
    font-size: 2.5em; }

  body {
    font-size: 22px; } }
blockquote {
  font-family : $sans-serif;
  font-size: 1.7em;
  font-weight: 700;
  color: #1FB7AF;
  line-height: 1.50;
  text-align: left;
  font-style: italic;
  margin: 2em auto; }

blockquote.gradient {
  background: -webkit-gradient(linear, left top, left bottom, from(#1FB7AF), to(#40D4CD));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

blockquote.small {
  font-size: 1.25em;
  text-align: left;
  color: #4A4A4A;
  font-weight: 300; }

.related-posts h2, .related-posts .h2 {
  font-weight: 300;
  font-style: italic; }



