    margin-bottom: 10px;
    display: block;
    width: auto !important;
    text-align: center;
    /* Bitmap: */
    box-shadow: 0 2px 19px 0 rgba(185, 185, 185, 0.5);
    position: relative;
    top: 0;
    transition: box-shadow 600ms, top 300ms;
}

.owl-inspirations a:hover img {
    /* Bitmap Copy: */
    top: -5px;
    position: relative;
    box-shadow: 0 17px 19px 0 rgba(185, 185, 185, 0.94);
}

.owl-inspirations a {
    color: #989898;
    font-size: 14px;
    text-align: center;
    display: block;
    width: 190px;
}
.owl-inspirations .owl-nav
{
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    .owl-prev , .owl-next
    {
        pointer-events: all;
        background: rgba(255,255,255,.8) !important;
        position: absolute;
         top: 0;
        bottom: 0;
        width: 30px;
        font-size: 40px !important;
        opacity: 1;
        transition: opacity 300ms;
        
        
    }
    .owl-prev{        
        left: 0;               
    }
    .owl-next{
        right: 0;       
    }
    .owl-prev.disabled,.owl-next.disabled
    {
        opacity: 0;
    }
}

