.para {
    padding-top: 100px;
    padding-bottom: 100px;
    min-height: 500px;

    display: flex;

    align-items: center;

    &::before,
    &::after {
        content: ' ';
        height: 1px;
        display: block;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    p,
    ul,
    blockquote,
    a {
        color: white;
    }

    ul {
        list-style-type: disc;
    }
    &.blue {
        background: $para-blue;
        /* Path 2: */
    }
    &.light-blue {
        background: $para-light-blue;
        background-image: linear-gradient(-130deg, #40EAEE 0%, #38C4C7 100%);

        h1,
        h2,
        h3,
        h4,
        h5,
        p,
        ul,
        blockquote,
        a {
            color: #3c3c3c;
        }

    }
    &.purple {
        background: $para-purple;
        /* 090 Mars Party: */
        background-image: linear-gradient(0deg, #9B23EA 0%, #5F72BD 100%);
    }
    &.green {
        background: $para-green;
    }
    &.light-red {
        background: $para-lightred;

        /* 131 African Field: */
        /* 108 Le Cocktail: */
        background-image: linear-gradient(45deg, #C43A30 0%, #874DA2 100%);
        /* Path 2 Copy 2: */
        background-image: linear-gradient(-130deg, #EA7776 0%, #F7A2A1 99%, #F8A2A1 100%);

    }
}

