.modal-content {
    display: none;
    position: fixed;
    width: 100%;
    max-width: 100vw;
    margin:0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #3FCACD;
    padding: 30px;
    z-index: 9999;
    opacity: 0;
    transition: opacity 200ms, transform 200ms cubic-bezier(0.49, -0.32, 0.34, 1.29);
    transform: scale(0.5);
}

.modal-content p,
.modal-content a {
    color: #444444 !important;
}

.modal-content hr {
    border-color: #444444;
}

.modal-content.in {
    opacity: 1;
    transform: scale(1);
}

.modal-overlay {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    /* Rectangle: */
    /* Rectangle: */
    background: rgba(0, 0, 0, 0.73);
    z-index: 9998;
    opacity: 0;
    transition: opacity 400ms;
}

.modal-overlay.in {
    opacity: 1;
}


